	module.exports = {"data":{"wordpressPost":{"title":"Kelly Osbourne Just Dyed Her Hair And She&#8217;s Unrecognisable","content":"<p><a href=\"https://www.instagram.com/kellyosbourne/\">Kelly Osbourne</a> has rocked a killer shade of lilac forever.</p>\n<p>Well, okay, that&#8217;s not strictly true. But she has been that colour for a damn long time. Which is kind of odd when you think about it because it&#8217;s more a temporary hair colour. You know what I mean. A shade you dye your hair for a little fun on Friday night before returning to your regular ashy blonde or, glossy chestnut brown on Monday.</p>\n<p>But not Kelly. A rebel born and bred, Kelly rocked that colour till we started to think it was natural. And just when we did &#8211; she changed it up and in a major way.</p>\n<p>Kelly is now a bold, bright shade of orange and she looks INCREDIBLE.</p>\n<figure id=\"attachment_3008\" style=\"width: 475px\" class=\"wp-caption alignnone\"><img class=\"size-full wp-image-3008\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02015956/J-Kelly-no-comments.jpg\" alt=\"\" width=\"475\" height=\"585\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02015956/J-Kelly-no-comments.jpg 475w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02015956/J-Kelly-no-comments-244x300.jpg 244w\" sizes=\"(max-width: 475px) 100vw, 475px\" /><figcaption class=\"wp-caption-text\">Ta dah! Killer colour. Image: Instagram/@Kellyosbourne</figcaption></figure>\n<p>&nbsp;</p>\n<p>Kelly, who has been quoted as saying she&#8217;s been bullied for her bold hair styles and even her fashion choices, debuted her new look on Instagram alongside the caption, &#8216;Welcome to the new ME!!!!!!&#8217;.</p>\n<p>And a new Kelly she most certainly is. Her hair colour is fierce and her makeup too. She&#8217;s wearing beautiful creamy foundation, a hint of bronze blush and natural rosy lip. Her eyes are stunning with a wash of peach shadow, a heavy retro-style liner and lashes, lashes, lashes.</p>\n<p>Girl looks good!</p>\n<p>And her fans think so too. A raft of followers posted comments saying how much they adore this new, somehow feminine look with many mentioning that Kelly now looks more like her mother Sharon than ever. Sharon, of course, is pretty fly and Kelly&#8217;s fans clearly mean the comments to be flattering.</p>\n<p>I see the resemblance &#8211; absolutely. But what I see most is a very beautiful young woman wearing a colour that works. Fingers crossed she keeps it for a while. And expect to see the shade a lot this spring and summer.</p>\n<p>Yes, the colour is bang on trend.</p>\n<p>A little too bold for you? Try a single chunk of hair, sort of a thick streak or even, have your stylists dip your ends. And remember, says <a href=\"http://raw.com.au\">Anthony Nader at RAW</a>, you can always dye it back. Phew!</p>\n<img class=\"alignnone size-full wp-image-3004\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02014505/J-Kelly-orange.jpg\" alt=\"\" width=\"811\" height=\"590\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02014505/J-Kelly-orange.jpg 811w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02014505/J-Kelly-orange-300x218.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02014505/J-Kelly-orange-768x559.jpg 768w\" sizes=\"(max-width: 811px) 100vw, 811px\" />\n<p>&nbsp;</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02013659/Lilac-no-comments.tiff"},"categories":[{"slug":"hair","name":"Hair"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"92f39bc0-15e2-53c6-b79d-78fedba9202d"}}

